import { useEffect, useState } from 'react';
import { GithubRepository } from 'models/github';
import { fetchAllLubyconRepos } from 'remotes/github';

interface Props {
  limit?: number;
}
export function useLubyconRepos({ limit }: Props = {}) {
  const [repos, setRepos] = useState<GithubRepository[]>([]);

  useEffect(() => {
    (async () => {
      const response = await fetchAllLubyconRepos();
      const repos = response.data
        .sort((a, b) => new Date(b.pushed_at).getTime() - new Date(a.pushed_at).getTime())
        .map<GithubRepository>(({ name, description, html_url, language }) => ({
          title: name,
          description,
          link: html_url,
          language,
        }));
      setRepos(limit != null ? repos.slice(0, limit) : repos);
    })();

    return () => {
      setRepos([]);
    };
  }, [limit]);

  return repos;
}
