import { makeStyles, Theme, Typography } from '@material-ui/core';
import { Link } from 'gatsby';
import React from 'react';
import { Magazine } from 'src/models';
import Card from './Card';

const useStyles = makeStyles<Theme>(() => ({
  title: {
    fontWeight: 'bold',
    lineHeight: 1.2,
  },
  description: {
    lineHeight: 1.2,
  },
}));

interface Props {
  magazine: Magazine;
}
const MagazineCard = ({ magazine }: Props) => {
  const styles = useStyles();

  return (
    <Link to={magazine.fields.path}>
      <Card image={magazine.frontmatter.thumbnail.childImageSharp.fixed.src}>
        <div>
          <Typography display="block" component="h3" variant="subtitle1" className={styles.title} gutterBottom={true}>
            {magazine.frontmatter.title}
          </Typography>
          <Typography display="block" variant="caption" className={styles.description}>
            {magazine.frontmatter.excerpt}
          </Typography>
        </div>
      </Card>
    </Link>
  );
};

export default MagazineCard;
