import React, { useMemo } from 'react';
import Page from 'components/Layout/Page';
import { Button, Container, Grid, makeStyles, Theme } from '@material-ui/core';
import MainJumbotron from 'src/components/Jumbotrons/MainJumbotron';
import DescriptionSection from 'components/DescriptionSection';
import { useAllMembers } from 'hooks/useAllMembers';
import MemberCard from 'components/Cards/ImageCard/MemberCard';
import { useLubyconMagazines } from 'hooks/posts/useLubyconMagazines';
import MagazineCard from 'components/Cards/ImageCard/MagazineCard';
import intersection from 'lodash/intersection';
import Emoji from 'components/Utils/Emoji';
import { useLubyconRepos } from 'hooks/useLubyconRepos';
import RepositoryCard from 'components/Cards/RepositoryCard';
import { useBlogFeeds } from 'src/hooks';
import PostCard from 'components/Cards/PostCard';
import { convertBlogFeedToPost } from 'src/utils/posting';
import RouteLink from 'components/Utils/RouteLink';

const useStyles = makeStyles<Theme>(({ breakpoints, spacing }) => ({
  container: {
    [breakpoints.up('lg')]: {
      maxWidth: 1088,
    },
  },
  readmoreLink: {
    textDecoration: 'none',
  },
  readmore: {
    marginTop: spacing(4),
    textDecoration: 'none',
    color: 'inherit',
  },
  contact: {
    marginTop: spacing(4),
    textDecoration: 'none',
  },
}));

const Index = () => {
  const styles = useStyles();
  const members = useAllMembers();
  const magazines = useLubyconMagazines();
  const filteredMagazines = useMemo(() => {
    return magazines.filter(magazine => intersection(magazine.frontmatter.tags, ['멘토링', '회고']).length > 0);
  }, [magazines]);
  const repos = useLubyconRepos({ limit: 9 });
  const blogs = useBlogFeeds({ limit: 9 });

  return (
    <Page pageName="Home">
      <MainJumbotron />
      <Container className={styles.container}>
        <DescriptionSection
          title="루비콘은 어떤 팀인가요?"
          description={`루비콘은 지난 2014년에 결성된 개발자와 디자이너들의 토이 프로젝트 팀입니다.\n멤버들은 일상 속의 아이디어나 새롭게 해보고 싶은 기술을 들고와서 다른 멤버들과 공유하고, 매일 새로운 것들에 도전하면서 다양한 프로젝트를 진행하고 있습니다.`}
        >
          <Grid container spacing={2}>
            {members.map(member => (
              <Grid key={member.id} item xs={12} sm={4} lg={3}>
                <MemberCard member={member} />
              </Grid>
            ))}
          </Grid>
        </DescriptionSection>
        <DescriptionSection
          title="Growth and Share"
          description={`루비콘은 성장과 공유라는 아젠다를 실현하기 위해 멘토링 프로젝트나 칫챗과 같은 몇 가지 활동을 진행하고 있습니다.`}
        >
          <Grid container spacing={2}>
            {filteredMagazines.map(magazine => (
              <Grid key={magazine.fields.path} item xs={12} lg={3}>
                <MagazineCard magazine={magazine} />
              </Grid>
            ))}
          </Grid>
        </DescriptionSection>
        <DescriptionSection
          title="포스팅"
          description={`루비콘은 빠르고 방향성 있는 성장을 지향하고 있습니다.\n루비콘 팀의 멤버들은 꾸준한 학습과 팀 내 공유로 이런 성장을 유지하려고 노력하고 있습니다.`}
        >
          <Grid container spacing={2}>
            {blogs.map(blog => (
              <Grid key={blog.link} item xs={12} sm={4}>
                <PostCard post={convertBlogFeedToPost(blog)} />
              </Grid>
            ))}
          </Grid>
          <Grid container>
            <Grid item component={RouteLink} xs={12} className={styles.readmore} to="/blogs">
              <Button variant="outlined" size="large" fullWidth={true}>
                더 보기&nbsp;
                <Emoji emoji="👀" name="read-more" />
              </Button>
            </Grid>
          </Grid>
        </DescriptionSection>
        <DescriptionSection
          title="Projects"
          description={`루비콘의 모든 프로젝트는 Github을 사용하여 진행되고 있고,\n루비콘 멤버 뿐 아니라 멘토링 프로젝트에 참여한 멘티들도 함께 참여할 수 있는 프로젝트를 지속적으로 이어나가고 있습니다`}
        >
          <Grid container spacing={2}>
            {repos.map(repo => (
              <Grid key={repo.title} item xs={12} sm={4}>
                <RepositoryCard repository={repo} />
              </Grid>
            ))}
          </Grid>
        </DescriptionSection>
        <DescriptionSection
          title="Contact Us"
          description="제휴 문의나 루비콘이 진행하고 있는 프로젝트에 대한 문의는 lubycon@gmail.com으로 보내주세요!"
        >
          <Grid container>
            <Grid item component="a" xs={12} href="mailto:lubycon@gmail.com" className={styles.contact} target="_blank">
              <Button variant="outlined" color="primary" size="large" fullWidth={true}>
                지금 바로 문의하기
                <Emoji emoji="🙋‍♂️" name="contact" />
              </Button>
            </Grid>
          </Grid>
        </DescriptionSection>
      </Container>
    </Page>
  );
};

export default Index;
