import { makeStyles, Paper, Theme, Typography } from '@material-ui/core';
import { yellow } from '@material-ui/core/colors';
import React, { useMemo } from 'react';
import { GithubRepository } from 'models/github';
import RouteLink from 'components/Utils/RouteLink';
import { LANGUAGE_COLORS } from 'constants/github';
import AnimatedListItem from 'components/Animated/AnimatedListItem';

const useStyles = makeStyles<Theme, { languageColor: string }>(({ spacing }) => ({
  root: {
    padding: spacing(2),
    transition: 'transform 0.2s ease-in-out',
    '&:hover': {
      transform: 'translateY(-10px)',
    },
  },
  header: {
    marginBottom: spacing(1),
  },
  title: {
    display: 'flex',
    alignItems: 'center',
    '& i': {
      marginRight: spacing(1),
      color: yellow['700'],
    },
    '& h3': {
      fontWeight: 'bold',
    },
  },
  link: {
    textDecoration: 'none',
  },
  language: {
    display: 'flex',
    alignItems: 'center',
  },
  languageTitle: {
    fontSize: 12,
  },
  languageMark: {
    display: 'inline-block',
    borderRadius: '50%',
    width: 12,
    height: 12,
    backgroundColor: ({ languageColor }) => languageColor,
    marginRight: spacing(1),
  },
}));

interface Props {
  repository: GithubRepository;
}
const RepositoryCard = ({ repository }: Props) => {
  const languageColor = useMemo(() => {
    const lang = repository.language as keyof typeof LANGUAGE_COLORS;
    const colors = LANGUAGE_COLORS[lang];
    return colors ?? '#000';
  }, [repository.language]);
  const styles = useStyles({ languageColor });

  return (
    <RouteLink to={repository.link} className={styles.link}>
      <AnimatedListItem>
        <Paper className={styles.root}>
          <div className={styles.header}>
            <div className={styles.title}>
              <i className="fas fa-code-branch" />
              <Typography variant="subtitle2" component="h3">
                {repository.title}
              </Typography>
            </div>
            <Typography variant="caption" color="textSecondary">
              {repository.description}
            </Typography>
          </div>
          <div className={styles.language}>
            <span className={styles.languageMark} />
            <Typography className={styles.languageTitle} color="textSecondary">
              {repository.language}
            </Typography>
          </div>
        </Paper>
      </AnimatedListItem>
    </RouteLink>
  );
};

export default RepositoryCard;
